import React from 'react';
import './style.css';
import Logo from "../../../images/kate.jpg";
import Section from "../../organisms/Section";

const AboutMe = () => {
    return (
        <Section
            title="O mnie"
            extraHeaderContent={
                <img className="article__image section__aboutMePortrait" src={Logo} alt="Katarzyna aoba"/>
            }
            body={
                <>
                    <p>
                        Hej, mam na imi Kasia i jestem tutaj, poniewa zainteresowaam si programowaniem.
                        Bardzo mio mi bdzie zaprosi ci do zgbienia reszty strony.
                    </p>
                    <p>
                        W wolnych chwilach uwielbiam truchta po lesie. Zawodowo zajmowaam si ochron danych osobowych, wszelkiego rodzaju wsparciem technicznym
                        (helpdesk), miniserwisem sprztw IT i ukadaniem procesw i instrukcji.</p>
                    <p>
                        Od dziecka marzyam o programowaniu. Dopiero po swoich trzydziestych urodzinach
                        uwiadomiam
                        sobie, e nasta moment na powrcenie do marzenia z dziecistwa.
                    </p>
                </>
            }
        />
    )
};

export default AboutMe;