        body: (
            <>
                <Blockquote>
                    <b>let</b> nazwa = warto;
                </Blockquote>
                <p>Nazwa zmiennej ma znaczenie  inaczej ni w przypadku <i>HTML</i>, w <i>JavaScript</i> rnice
                    liter s wane, wic przypisujc warto do zmiennej <i>nazwa</i>, nie bdzie ona przypisana
                    automatycznie do zmiennych <i>Nazwa</i>, <i>NaZwA</i> itd.
                </p>
                <p>Nazwa zmiennej musi zaczyna si od litery, "_" (ang. <i>underscore</i>) lub "$".</p>
                <p>Nazwa zmiennej jak najbardziej moe zawiera cyfry, ale nie mog one by na pierwszym miejscu.
                </p>
                <p>Jest kilka sw wbudowanych do <i>JavaScript-a</i>, ktrych nie moemy uy do nazwania zmiennej, np.
                    <i>var</i>, <i>let</i>, <i>function</i> itd. <a href="https://mothereff.in/js-variables"
                                                                    target="_blank"
                                                                    title="JavaScript variable name validator">Przydatna
                        stronka, do
                        sprawdzenia, czy nazwa, ktrej chcesz uy, nie jest zajta</a>.
                </p>
                <p>Przy nazywaniu zmiennych uywamy konwencji <i>camelCase</i> oraz liter alfabetu aciskiego..</p>
            </>
        )
