            <>
                <p>
                    Co zawieraj instrukcje?
                </p>
                <ListUnordered>
                    <ListItem><i>literay</i> - wartoci (np. napisy, liczby) wpisane w kod
                        rdowy,
                        kopiowane do kodu wykonywalnego podczas kompilacji;
                    </ListItem>
                    <ListItem><i>stae</i> - symbole (nazwy) reprezentujce ustalone
                        wartoci,
                        zastpowane tymi wartociami podczas kompilacji;
                    </ListItem>
                    <ListItem><i>wyraenia</i> - zapis oblicze;</ListItem>
                    <ListItem><i>zmienne lub nazwy zmiennych</i> - symbole reprezentujce
                        wartoci,
                        ktre mog zmienia w czasie wykonywania programu;
                    </ListItem>
                    <ListItem><i>sowa kluczowe</i> - symbole reprezentujce okrelone
                        dziaania
                        (np.
                        <i>for, do, while, if, else, return</i>);
                    </ListItem>
                    <ListItem><i>wywoania funkcji</i> - symbole oznaczajce miejsca w
                        programie,
                        w
                        ktrych naley wykona instrukcje wchodzce w skad treci danej funkcji (wyniki wykonania
                        funkcji te "pojawiaj si" w tych miejscach);
                    </ListItem>
                    <ListItem><i>znaki przerwy;</i></ListItem>
                    <ListItem><i>spacje;</i></ListItem>
                    <ListItem><i>tabulacje poziome;</i></ListItem>
                    <ListItem>przejcia do nastpnego wiersza (s pomijane podczas
                        kompilacji,
                        tylko
                        istniej pewne ograniczenia dotyczce miejsc, w ktrych moe by umieszczone przejcie do
                        nastpnego wiersza, o ile nie zostanie poprzedzone znakiem "\" (<i>backslash</i>));
                    </ListItem>
                    <ListItem>komentarze, ktre umieszczamy po symbolu "//" do koca
                        wiersza,
                        lub
                        midzy symbolami "/* oraz */" (komentarze s ignorowane przez kompilator).
                    </ListItem>
                </ListUnordered>
            </>
